
.modal[data-v-e6f3825a] {
  /*弹框*/
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  vertical-align: middle;
}
.modal .icon-close[data-v-e6f3825a] {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #ccc;
  font-size: 24px;
  cursor: pointer;
}
.modal .icon-close[data-v-e6f3825a]:hover {
  opacity: 0.8;
}
.modal .box[data-v-e6f3825a] {
  display: inline-block;
  position: relative;
  max-width: 1000px;
  padding: 20px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
  box-shadow: 3px 3px 11px 9px #d9d8dd;
  color: #555;
  line-height: 150%;
  text-align: left;
}
.modal .box .main-icon[data-v-e6f3825a] {
  font-size: 28px;
}
.modal .bg[data-v-e6f3825a] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
}



