/*
* 重置默认样式
* */
/* CSS Document */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
abbr,
article,
aside,
audio,
canvas,
datalist,
details,
figure,
footer,
hgroup,
header,
mark,
menu,
meter,
nav,
output,
progress,
section,
time,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
.article,
aside,
audio,
canvas,
datalist,
details,
figure,
footer,
hgroup,
header,
mark,
menu,
meter,
nav,
output,
progress,
section,
time,
video {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul,
li {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
/* remember to define focus styles! */
:focus {
  outline: 0;
}
/* remember to highlight inserts somehow! */
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
legend {
  display: none;
}
img {
  vertical-align: middle;
}
input,
button {
  overflow: visible;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
/*
* 变量样式
* */
html,
body {
  overflow-y: auto;
  min-width: 1200px;
  height: 100%;
  background-color: #f0f3f4;
  color: #555;
  font-size: 14px;
  font-family: 微软雅黑;
}
#app {
  width: 100%;
  height: 100%;
}
#nav {
  position: absolute;
  z-index: 2;
  width: 220px;
  min-height: 100%;
  background-color: #1c2b36;
  -moz-box-shadow: 0 0 2px #ccc;
  -webkit-box-shadow: 0 0 2px #ccc;
  box-shadow: 0 0 2px #ccc;
}
#main {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 65px 15px 15px 235px;
  box-sizing: border-box;
}
#header {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 50px;
  -moz-box-shadow: 0 0 5px #aaa;
  -webkit-box-shadow: 0 0 5px #aaa;
  box-shadow: 0 0 5px #aaa;
}
#footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  border-top: 1px solid #eee;
  color: #666;
  font-size: 12px;
  text-align: center;
  line-height: 50px;
}
#content {
  position: relative;
  width: 100%;
  min-height: 100%;
  padding-bottom: 20px;
  box-sizing: border-box;
  background-color: #fff;
  -moz-box-shadow: 0 0 2px #ccc;
  -webkit-box-shadow: 0 0 2px #ccc;
  box-shadow: 0 0 2px #ccc;
}
.container {
  padding: 10px 10px 20px;
}
.search-add {
  position: relative;
  height: 40px;
  padding: 10px;
}
.hover {
  cursor: pointer;
}
.hover:hover {
  text-decoration: underline;
}
@font-face {
  font-family: 'iconfont';
  /* project id 273341 */
  src: url('//at.alicdn.com/t/font_5rfpz1puzmgc766r.eot');
  src: url('//at.alicdn.com/t/font_5rfpz1puzmgc766r.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_5rfpz1puzmgc766r.woff') format('woff'), url('//at.alicdn.com/t/font_5rfpz1puzmgc766r.ttf') format('truetype'), url('//at.alicdn.com/t/font_5rfpz1puzmgc766r.svg#iconfont') format('svg');
}
.icon {
  display: inline-block;
  color: #555;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  font-family: 'iconfont';
  vertical-align: middle;
}
.table-box {
  padding: 10px;
}
.table {
  width: 100%;
}
.table th {
  padding: 10px 0;
  background-color: #f6f8f8;
  font-weight: normal;
  line-height: 22px;
  text-align: center;
}
.table td {
  padding: 10px 0;
  border-bottom: 1px solid #edf1f2;
  font-size: 12px;
  line-height: 22px;
  text-align: center;
}
.table .tools li {
  display: inline-block;
  padding: 0 5px;
}
/*input表格*/
.input-form {
  width: 100%;
  margin-top: 40px;
}
.input-form .caption {
  color: #4a4a4a;
  font-size: 20px;
}
.input-form tr.line {
  border-bottom: 2px solid rgba(221, 221, 221, 0.8);
}
.input-form tr.line th,
.input-form tr.line td {
  padding-bottom: 20px;
}
.input-form th {
  width: 150px;
  padding: 14px 14px 0 0;
  color: #424f63;
  font-weight: normal;
  font-size: 14px;
  font-family: Light;
  text-align: right;
}
.input-form td {
  padding: 0px 0 14px 14px;
}
.input-form td .hint {
  display: block;
  height: 14px;
  margin: 5px 0 -5px;
  padding-left: 3px;
  color: red;
  font-size: 12px;
  line-height: 14px;
}
.input-form td .hint.short {
  line-height: 20px;
}
.btn-default:disabled {
  opacity: 0.5;
  cursor: default;
}
.btn {
  padding: 5px 10px;
  border: 1px solid #bbb;
  border-radius: 4px;
  background-color: #fff;
  color: #555;
  cursor: pointer;
}
.btn:hover {
  background-color: #f0f0f0;
}
.btn.btn-primary {
  border: 1px solid #19a9d5;
  background-color: #19a9d5;
  color: #fff;
}
.btn.btn-primary:hover {
  background-color: #1691b6;
}
.btn.btn-success {
  border: 1px solid #5cb85c;
  background-color: #5cb85c;
  color: #fff;
}
.btn.btn-success:hover {
  background-color: #449d44;
}
.btn.btn-danger {
  border: 1px solid #d9534f;
  background-color: #d9534f;
  color: #fff;
}
.btn.btn-danger:hover {
  background-color: #c9302c;
}
.btn.btn-warning {
  border: 1px solid #f0ad4e;
  background-color: #f0ad4e;
  color: #fff;
}
.btn.btn-warning:hover {
  background-color: #ec97ef;
}
.input {
  width: 184px;
  height: 30px;
  padding: 0 10px;
  border: 1px solid #cfdadd;
  border-radius: 4px;
  box-sizing: border-box;
  color: #555;
  line-height: 30px;
}
.input.input-long {
  width: 525px;
}
.input.warning {
  border: 1px solid #f0ad4e;
  color: #f0ad4e;
}
.input.primary {
  border: 1px solid #19a9d5;
  color: #19a9d5;
}
.input.success {
  border: 1px solid #5cb85c;
  color: #5cb85c;
}
.input.danger {
  border: 1px solid #d9534f;
  color: #d9534f;
}
.input:focus {
  border: 1px solid #19a9d5;
}
